import commentService from "../services/comments"

const commentReducer = (store = [], action) => {
  switch(action.type){
  case "CREATE_COMMENT":
    return [...store, action.data]
  case "INIT_COMMENTS":
    return action.data
  default:
    return store
  }
}

export const initializeComments = () => {
  return async dispatch => {
    const comments = await commentService.getAll()
    dispatch({
      type: "INIT_COMMENTS",
      data: comments
    })
  }
}

export const createComment = (author, content) => {
  return async dispatch => {
    const newComment = await commentService.createNew(author, content)
    dispatch({
      type: "CREATE_COMMENT",
      data: newComment
    })
  }
}


export default commentReducer