import React from "react"
import "../styles/containers.css"
import "../styles/texts.css"
import "../styles/buttons.css"
import reactLogo from "../images/reactLogo.svg"
import nodeLogo from "../images/nodeLogo.png"

const Footer = () => {
  return (
    <div className="footerContainer">
      <p className="footerText">Made by Niklas Impiö</p>
      <a href="https://github.com/maybet0morrow" className="footerText" >@GitHub</a>
      <br></br>
      <img src={nodeLogo} className="logo" alt="nodeLogo"></img>
      <img src={reactLogo} className="logo" alt="reactLogo"></img>
    </div>
  )
}

export default Footer