import axios from "axios"
//when running sepatate frontend
//const baseUrl = "http://localhost:3001/api/content"

// In production
const baseUrl = "/api/content"
let token = null

const getAll = async () => {
  const config = {
    headers: {"Authorization": token}
    //headers: {"Authorization": `bearer ${JSON.parse(window.localStorage.getItem("cvAppUser"))["token"]}`}
  }
  const response = await axios.get(baseUrl, config)
  return response.data
}

const setToken = (newToken) => {
  token = `bearer ${newToken}`
}

const createNew = async (content) => {
  const config = {
    headers: {"Authorization": token}
  }
  const object = {content, date: new Date()}
  const response = await axios.post(baseUrl, object, config)
  return response.data
}

export default {getAll, createNew, setToken}