import React from "react"
import {connect} from "react-redux"
import {login} from "../reducers/loginReducer"
import {notify} from "../reducers/notificationReducer"
import {initializeComments} from "../reducers/commentReducer"
import {initializeContent} from "../reducers/contentReducer"


export const LoginForm = (props) => {
  const attemptLogin = async (event) => {
    console.log("attempting login")
    event.preventDefault()
    const username = event.target.username.value
    const password = event.target.password.value
    event.target.username.value = ""
    event.target.password.value = ""
    const user = await props.login(username,password, props.notify)
    //init content if login was succesfull.
    if(user){
      props.initializeComments()
      props.initializeContent()
    }
    /*
    if (user === null){
      props.notify("Invalid credentials",true,5)
    }else{
      if(!user.activated){
        props.notify("Welcome! \n(Just showing off the notification thingy.)")
      }
    }
    */
  }

  return(
    <div className="loginContainer centerAlign">
      <p className="headerTextLarge bottomPadding30">Kirjaudu</p>

      <form name="loginForm" onSubmit={attemptLogin}>
        <div className="inputContainer">
          <input name="username" className="input" placeholder="Käyttäjätunnus" maxLength="32" autoComplete="off"/>
          <div className="inputFocusLine"/>

        </div>
        <div className="inputContainer">
          <input name="password" className="input" type="password" placeholder="Salasana" maxLength="64"/>
          <div className="inputFocusLine"></div>
        </div>

        <button className="loginButton rippleButton">Kirjaudu</button>
      </form>
    </div>

  )
}

export default connect(
  null,
  {login, notify,   initializeComments, initializeContent}
)(LoginForm)