import contentService from "../services/content"

const contentReducer = (store = [], action) => {
  switch(action.type){
  case "CREATE":
    return [...store, action.data]
  case "INIT":
    return action.data
  default:
    return store
  }
}

export const initializeContent = () => {
  return async dispatch => {
    const content = await contentService.getAll()
    dispatch({
      type: "INIT",
      data: content[0],
    })
  }
}

export const createContent = (blogObject) => {
  return async dispatch => {
    const newContent = await contentService.createBlog(blogObject)
    dispatch({
      type: "CREATE",
      data: newContent
    })
  }
}

export default contentReducer