import React, {useEffect} from "react"
import {connect} from "react-redux"
import {
  BrowserRouter as Router,
} from "react-router-dom"

import commentService from "./services/comments"
import contentService from "./services/content"

import {initializeComments} from "./reducers/commentReducer"
import {initializeContent} from "./reducers/contentReducer"
import {login, logout, initLoggedUser} from "./reducers/loginReducer"
import {notify} from "./reducers/notificationReducer"


import Footer from "./components/Footer"
import Menu from "./components/Menu"
import Content from "./components/Content"
import Notification from "./components/Notification"
import CommentSection from "./components/CommentSection"
import LoginForm from "./components/LoginForm"

import "./styles/containers.css"
import "./styles/texts.css"
import "./styles/buttons.css"





const App = (props) => {
  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem("cvAppUser")
    if(loggedUserJSON){
      const user = JSON.parse(loggedUserJSON)
      props.initLoggedUser(user)
      commentService.setToken(user.token)
      contentService.setToken(user.token)
      console.log("initalizing content")
      props.initializeContent()
      props.initializeComments()
    }
  }, [])


  return (
    <div className="appContainer">
      <Router>

        {props.loggedUser === null ?
          <LoginForm onSuccess={() => {}}/>
          :
          <div className="appContainer">
            <div className="gridContainer">
              <Menu/>
              <Content/>
              <CommentSection/>

            </div>
            <Footer/>
          </div>

        }
        {(props.notification.message !== null)?
          <Notification/>
          :
          <div></div>
        }

      </Router>
    </div>
  )
}

/*

*/

const mapStateToProps = (state) => {
  return {
    users: state.users,
    notification: state.notification,
    loggedUser : state.user,
    content : state.content,
    comments: state.comments
  }
}

const mapDispatchToProps = {
  notify,
  initializeContent,
  login,
  logout,
  initLoggedUser,
  initializeComments

}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)
