import React from "react"
import {connect} from "react-redux"
import {logout} from "../reducers/loginReducer"
import {notify} from "../reducers/notificationReducer"
import "../styles/containers.css"
import "../styles/texts.css"
import "../styles/buttons.css"

export const Menu = (props) => {
  const logout = () => {
    console.log("Logging out")
    props.logout(props.notify)
  }

  return (
    <div className="headerMenuContainer">
      <div className="gridHeaderMenu">
        <div className="headerMenuLogo">
          <p className="logoText">Niklas Impiö Cv App</p>
        </div>

        <button className="logoutButton rippleButton" onClick={logout}>Kirjaudu Ulos</button>

      </div>
    </div>
  )
}

export default connect(
  null,
  {notify, logout}
)(Menu)