import React from "react"
import {connect} from "react-redux"

import "../styles/containers.css"
import "../styles/texts.css"
import "../styles/buttons.css"


const Content = (props) => {
  return (
    <div className="contentContainer">
      {props.content !== undefined && props.content.length !== 0?
        <div className="contentGrid">
          <div className="contentHeaderBox">
            <p className="headerTextLarge">{props.content.name}</p>
            <div className="addressLine">
              <span className="textPrimary">{props.content.address} </span>
              <span className="textPrimary">{props.content.zipAndCity}</span>
              <p className="textPrimary">{props.content.email}</p>
              <p className="textPrimary">{props.content.phone}</p>
            </div>

          </div>
          <div className="contentBox contentProfileBox">
            <p className="headerText">Profiili</p>
            <ul className="contentList">
              {
                props.content.profile.map((element,i) => (
                  <li key={i} className="contentList">
                    <p className="textContent"> {element}</p>
                  </li>
                ))
              }
            </ul>

          </div>
          <div className="contentBox contentEducationBox">
            <p className="headerText">Koulutus</p>
            <p className="textPrimary"></p>
            <ul className="contentList">
              {
                props.content.education.map((element,i) => (
                  <li key={i} className="contentList">
                    <p className="textContent">{element.name}</p>
                    <p className="textContent">
                      {element.degree} {element.graduation}
                    </p>
                    {(element.link !== undefined && element.link !== "")?
                      <p className="textContent"> Kandidaatintyö: <a className="textContent" href={element.link}>Linkki</a></p>
                      :
                      <div></div>
                    }
                  </li>
                ))
              }
            </ul>

          </div>
          <div className="contentBox contentExperienceBox">
            <p className="headerText">Työkokemus</p>
            <ul className="contentList">

              {
                props.content.jobHistory.map((element,i) => (
                  <li key={i} className="contentList">
                    <p className="textContent"> {element.jobTitle}</p>
                    <p className="textContent">{element.employer}</p>
                    <p className="textContent">{element.duration}</p>
                  </li>
                ))
              }
            </ul>

          </div>
          <div className="contentBox contentSkillsBox">
            <p className="headerText">Taidot</p>
            <ul className="contentList">
              {
                props.content.skills.map((element,i) => (
                  <li key={i} className="contentList">
                    <p className="textContent"> {element}</p>
                  </li>
                ))
              }
            </ul>

          </div>
        </div>
        :
        <div className="noContent"></div>
      }


    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    content: state.content
  }
}

export default connect(
  mapStateToProps
)(Content)